/* styling the sidebar */
#simplecharm-homepage-sidebar, #simplecharm-postpage-sidebar,
.simplecharm-post ,
.simplecharm-post-container{
    border: 1px solid #300f00;
    width: calc(100% - 15px);
    padding: 2rem;
    position: relative;
    color:#32373c;
}
#simplecharm-homepage-sidebar, #simplecharm-postpage-sidebar {
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    text-align:center;
}
#simplecharm-homepage-sidebar li{
    list-style:none;
}
#simplecharm-homepage-sidebar a{
    color:currentcolor;
}

#simplecharm-homepage-sidebar::before,#simplecharm-postpage-sidebar::before,
.simplecharm-post::before,
.simplecharm-post-container::before {
    content: '';
    position: absolute;
    height: 20px;
    width: 20px;
    background: #000;
    top: 0;
    left: 0;
}

#simplecharm-homepage-sidebar::after, #simplecharm-postpage-sidebar::after,
.simplecharm-post::after {
    content: '';
    position: absolute;
    height: 20px;
    width: 20px;
    background: #000;
    bottom: 0;
    right: 0;
}