/* Wordpress Default Styles*/
.wp-caption {
  max-width: 100%;
  margin-bottom: 1.5em;
  padding: 0;
  background-color: #f5f5f5;
  border: 1px solid #e3e3e3;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1);
  -webkit-border-radius: 3px;
  border-radius: 3px;
  -webkit-box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1);
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1);
}

.wp-caption img {
  margin: 0;
  padding: 0;
  border: 0 none;
  max-width: 100%;
  height: auto;
}

.wp-caption p.wp-caption-text {
  font-size: 0.875em;
  line-height: 1.6;
  margin: 0;
  padding: 0.5em;
  text-align: center;
}

.sticky {
  background-color: #f5f5f5;
  border: 1px solid #dd0101;
  border-left-width: 5px;
}

.sticky img {
  margin: 0;
  padding: 0;
  border: 0 none;
  max-width: 100%;
  height: auto;
}

.sticky p.wp-caption-text {
  font-size: 0.875em;
  line-height: 1.6;
  margin: 0;
  padding: 0.5em;
  text-align: center;
}

.sticky .wp-caption {
  max-width: 100%;
  margin-bottom: 1.5em;
  padding: 0;
  background-color: #f5f5f5;
  border: 1px solid #e3e3e3;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1);
  -webkit-border-radius: 3px;
  border-radius: 3px;
  -webkit-box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1);
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1);
}

.sticky .wp-caption img {
  margin: 0;
  padding: 0;
  border: 0 none;
  max-width: 100%;
  height: auto;
}

.sticky .wp-caption p.wp-caption-text {
  font-size: 0.875em;
  line-height: 1.6;
  margin: 0;
  padding: 0.5em;
  text-align: center;
}

.gallery-caption {
  font-size: 0.875em;
  line-height: 1.6;
  margin: 0;
  padding: 0.5em;
  text-align: center;
}

.alignleft {
  float: left;
  margin-right: 1.5em;
}

.alignright {
  float: right;
  margin-left: 1.5em;
}

.aligncenter {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.wp-caption.alignleft {
  margin-right: 1.5em;
}

.wp-caption.alignright {
  margin-left: 1.5em;
}

.wp-caption.aligncenter {
  margin-right: auto;
  margin-left: auto;
}

a img.alignleft {
  margin-right: 1.5em;
}

a img.alignright {
  margin-left: 1.5em;
}

a img.aligncenter {
  margin-right: auto;
  margin-left: auto;
}

.wp-caption.alignnone, a img.alignnone {
  margin-right: 0;
  margin-left: 0;
}

.wp-caption.alignnone {
  margin-bottom: 1.5em;
}

.wp-caption.alignnone img {
  margin: 0;
  padding: 0;
  border: 0 none;
  max-width: 100%;
  height: auto;
}

.wp-caption.alignnone p.wp-caption-text {
  font-size: 0.875em;
  line-height: 1.6;
  margin: 0;
  padding: 0.5em;
  text-align: center;
}

.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.screen-reader-text:focus {
  background-color: #f1f1f1;
  border-radius: 3px;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip: auto !important;
  color: #21759b;
  display: block;
  font-size: 14px;
  font-size: 0.875rem;
  font-weight: bold;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
}

.post-meta a {
  color: #005882;
}

#simplecharm-content[tabindex="-1"]:focus, #comments[tabindex="-1"]:focus {
  outline: 0;
}

/* Style for posts*/
pre {
  overflow-y: scroll;
}

code {
  font-family: ui-sans-serif ui-serif sans-serif serif monospace;
}

/* Utility Classes and Others */
.simplecharm-text-center {
  text-align: center;
}

.simplecharm-model-link a,
.simplecharm-model-link {
  text-decoration: none;
}

.block {
  display: block;
}

.simplecharm-model-shadow {
  text-shadow: 2px 1px 2px #00f;
}

.simplecharm-content {
  border: 1px solid #333;
  padding: 10px;
}

.simplecharm-model-link a,
.simplecharm-model-link,
.simplecharm-content a,
.simplecharm-main-navigation a,
.simplecharm-edit-profile-btn a,
.simplecharm-model-link p {
  color: #005e86;
}

.simplecharm-content p {
  margin: 10px 0;
  color: #070002;
}

.simplecharm-content a:visited,
.simplecharm-main-navigation a:visited,
.simplecharm-edit-profile-btn a:visited {
  color: #005881;
}

.simplecharm-content a,
.simplecharm-main-navigation a,
.simplecharm-edit-profile-btn a {
  color: #005e8b;
  text-decoration: none;
}

.simplecharm-content a:hover,
.simplecharm-content a:focus,
.simplecharm-main-navigation a:hover,
.simplecharm-main-navigation a:focus,
.simplecharm-edit-profile-btn a:hover,
.simplecharm-edit-profile-btn a:focus {
  text-decoration: underline;
}

.simplecharm-content a:focus,
.simplecharm-main-navigation a:focus,
.simplecharm-edit-profile-btn a:focus,
.simplecharm-content a:hover,
.simplecharm-main-navigation a:hover,
.simplecharm-edit-profile-btn a:hover {
  color: #005e86;
}

.simplecharm-flex {
  display: flex;
}

.simplecharm-m-auto {
  margin: auto;
}

.simplecharm-justify-center {
  justify-content: center;
}

/* styling header the nav menu */
.simplecharm-site-description {
  color: #75a42d;
  font-size: 1.2em;
  letter-spacing: 5px;
  padding: 5px 10px;
  background-color: rgba(0, 0, 0, 0.5);
  margin: 5px 0;
}

nav.simplecharm-main-navigation ul {
  list-style: none;
  position: relative;
  background: rgba(255, 255, 255, 0.4);
  margin: 0 0 5px 0;
  transition: all 0.4s;
  transition-delay: 0.2s;
}

nav.simplecharm-main-navigation ul .sub-menu {
  display: flex;
  flex-direction: column;
  float: left;
  width: 25%;
}

nav.simplecharm-main-navigation ul li {
  display: inline-block;
  margin: 10px;
}

nav.simplecharm-main-navigation ul li a {
  padding: 10px;
}

nav.simplecharm-main-navigation ul .sub-menu {
  position: absolute;
  width: 100%;
  left: 0;
  z-index: 12;
  display: flex;
  opacity: 0;
  pointer-events: none;
}

nav.simplecharm-main-navigation ul li:hover .sub-menu,
nav.simplecharm-main-navigation ul li:focus-within .sub-menu {
  opacity: 1;
  pointer-events: auto;
}

nav.simplecharm-main-navigation ul li a:hover,
nav.simplecharm-main-navigation ul li a:focus {
  text-decoration: underline;
}

/*styling single.php page and home page*/
.simplecharm-home-post-title a {
  text-decoration: none;
}

.simplecharm-post-category a {
  color: #005882;
}

.simplecharm-post-category {
  display: block;
  text-align: left;
  text-transform: uppercase;
  font-weight: bold;
  color: #000;
}

.simplecharm-single-container, .simplecharm-posts-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 10px;
  margin: 10px;
}

.post-author {
  text-decoration: underline;
}

.simplecharm-single-container div:nth-child(1), .simplecharm-post-container {
  grid-column: 1/3;
}

/* hide sidebar for mobile deveices */
@media (max-width: 768px) {
  .simplecharm-single-container div:nth-child(1),
  .simplecharm-post-container {
    grid-column: 1/4;
  }
  .simplecharm-single-container,
  .simplecharm-posts-container {
    grid-template-columns: 1fr;
    grid-gap: 0;
    margin: 0;
  }
  .simplecharm-header-contents:has(+ .simplecharm-header-image) {
    flex-direction: column;
  }
}
/* styling the sidebar */
#simplecharm-homepage-sidebar, #simplecharm-postpage-sidebar,
.simplecharm-post,
.simplecharm-post-container {
  border: 1px solid #300f00;
  width: calc(100% - 15px);
  padding: 2rem;
  position: relative;
  color: #32373c;
}

#simplecharm-homepage-sidebar, #simplecharm-postpage-sidebar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

#simplecharm-homepage-sidebar li {
  list-style: none;
}

#simplecharm-homepage-sidebar a {
  color: currentcolor;
}

#simplecharm-homepage-sidebar::before, #simplecharm-postpage-sidebar::before,
.simplecharm-post::before,
.simplecharm-post-container::before {
  content: "";
  position: absolute;
  height: 20px;
  width: 20px;
  background: #000;
  top: 0;
  left: 0;
}

#simplecharm-homepage-sidebar::after, #simplecharm-postpage-sidebar::after,
.simplecharm-post::after {
  content: "";
  position: absolute;
  height: 20px;
  width: 20px;
  background: #000;
  bottom: 0;
  right: 0;
}

/* styling the pagination */
nav.navigation.pagination {
  text-align: center;
  margin: 10px;
}

nav.navigation.pagination a {
  border: 1px solid #000;
  padding: 5px;
  color: currentcolor;
}

.simplecharm-main-navigation a {
  transition: all 0.3s;
}

.simplecharm-main-navigation a:hover,
.simplecharm-main-navigation a:focus {
  background: #000;
}

/*styling comment box */
.comment-body {
  border: 1px solid #090909;
  padding: 10px;
}

.wp-block-post-comments-form input[type=submit] {
  padding: 10px;
  background: #000;
  color: #fafafa;
  border: 3px solid rgb(68, 68, 167);
}

.wp-block-post-comments-form input[type=submit]:hover,
.wp-block-post-comments-form input[type=submit]:focus {
  background: rgba(21, 21, 21, 0.9490196078);
  border-color: #00f;
}

.bypostauthor .comment-body {
  position: relative;
  margin: 10px 0 0 0;
}

.bypostauthor .comment-body::before {
  position: absolute;
  content: "author";
  top: 0;
  right: 0;
  background: #f00;
  padding: 5px;
  border-radius: 0px 0px 0px 10px;
  color: #fff;
  font-weight: bold;
}

.reply a {
  text-decoration: none;
  color: currentColor;
  padding: 5px;
  border: 1px solid #000;
}

/*edit profile link / button*/
.simplecharm-edit-profile-btn {
  display: block;
}

.simplecharm-edit-profile-btn a {
  border: 1px solid currentColor;
  padding: 5px 10px;
  margin: auto;
}

.simplecharm-edit-profile-btn a:hover,
.simplecharm-edit-profile-btn a:focus {
  text-decoration: underline !important;
}

/* style the header */
.simplecharm-header-image img {
  width: 100%;
  height: auto;
}

.simplecharm-header {
  position: relative;
}

.simplecharm-header-contents:has(+ .simplecharm-header-image) {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: space-between;
  width: 95%;
  max-width: 1280px;
  row-gap: 0.5rem;
}

.simplecharm-header-contents a {
  color: #fff;
  background-color: #000;
  padding: 0.5rem 1rem;
  border-radius: 0.125rem;
}

/* styling the footer */
.simplecharm-theme-footer {
  background: #808080;
  padding: 20px 0;
  text-align: center;
  color: #000;
}

.simplecharm-theme-footer a {
  color: #021141;
}

/*search page navigation*/
.posts-navigation .nav-links {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
}

.posts-navigation .nav-links a {
  color: currentColor;
}

/*styling the search bar*/
.search-form {
  display: flex;
  gap: 10px;
  justify-content: center;
  margin-bottom: 5px;
}

.search-field, .search-submit {
  border: none;
  padding: 10px;
  text-align: center;
}

.search-field {
  font-family: sans-serif;
  font-size: 16px;
  height: 50px;
  text-align: left;
  border: 1px solid #0a0a0a;
}

.search-submit {
  font-family: sans-serif;
  font-size: 16px;
  cursor: pointer;
  display: block;
  height: 50px;
  padding-inline: 50px;
  border: 1px solid #0a0a0a;
}

.search-submit:hover {
  background: #000;
  color: #fff;
}

.search-field:focus {
  border: none;
  outline: none;
}